.box_grid{
    /* animation: animate 4s linear infinite; */
    position: relative;
    z-index: 99;
}
.box_grid2{
    /* animation: animate 4s linear infinite; */
    position: relative;
    z-index: 99;
    
}

.box_grid::before{
    content: '';
    position: absolute;
    width: 200px;
    height: 1500px;
    left: 38%;
    top: -130%;
    z-index: -90;
    box-shadow: 0 0 150px rgb(159, 238, 238),0 0 1500px rgb(25, 233, 233),0 0 2000px rgb(233, 250, 250);
    background-image:linear-gradient(to bottom right,#ec3188 40%, rgb(241, 71, 113) 100%,#f3abd3 40%); 
    animation: animate 5s linear infinite;
    transition: 0.5s;
}
.box_grid2::before{
    content: '';
    position: absolute;
    width: 1200px;
    height: 100px;
    left: -8%;
    top: 30%;
    z-index: -90;
    box-shadow: 0 0 150px rgb(159, 238, 238),0 0 1500px rgb(25, 233, 233),0 0 2000px rgb(233, 250, 250);
    background-image:linear-gradient(to bottom right,#ec3188 40%, rgb(241, 71, 113) 100%,#f3abd3 40%); 
    animation: animate 5s linear infinite;
    transition: 0.5s;
}

